/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #f8f9fa;
  color: #343a40;
  font-size: 16px;
  line-height: 1.8;
}

/* Links */
a {
  text-decoration: none;
  color: #007bff; /* Blue theme for links */
  transition: color 0.3s ease;
}

a:hover {
  color: #0056b3; /* Darker blue on hover */
}

/* Page Layout */
.page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Header Section */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 60px;
}

.header-content {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: flex-end; /* Align to the right */
  text-align: right;
  gap: 20px;
}

.profile-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  object-position: top center;
  margin-right: auto; /* Keep image on the left */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.profile-image:hover {
  transform: scale(1.1);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.header-text h1 {
  font-size: 2.5em;
  font-weight: bold;
}

.nav-links {
  display: flex;
  gap: 15px;
}

.nav-links a {
  font-size: 1.1em;
  color: #6c757d; /* Subtle gray */
}

.nav-links a:hover {
  color: #007bff;
}

/* Section Styles */
.section {
  padding: 40px 20px;
  margin-bottom: 60px;
  opacity: 1;
  transform: translateY(0);
  transition: opacity 0.8s ease, transform 0.8s ease;
  opacity: 0; /* Start invisible */
  transform: translateY(20px); /* Start slightly below */
  transition: opacity 0.8s ease-out, transform 0.8s ease-out; /* Smooth animation */
}

.section.visible {
  opacity: 1;
  transform: translateY(0);
}

h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #007bff; /* Primary color for headings */
}

p {
  font-size: 1rem;
  color: #555;
  margin-bottom: 20px;
}

/* Project Cards */
.project-card {
  display: flex;
  margin-bottom: 30px;
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
}

.project-image a {
  display: block;
  width: 150px; /* Smaller width */
  height: 120px; /* Smaller height */
  border-radius: 8px;
  overflow: hidden;
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project-content h3 {
  font-size: 2em;
  margin-bottom: 10px;
}

.project-content a {
  color: #007bff; /* Blue links */
  font-weight: bold;
  transition: color 0.3s ease;
}

.project-content a:hover {
  color: #0056b3; /* Darker blue on hover */
}

/* Experience Cards */
.experience-card {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.experience-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
}

.experience-content h3 {
  font-size: 1.8em;
  color: #333;
  margin-bottom: 10px;
}

.experience-content ul {
  list-style-type: disc;
  margin-left: 20px;
}

.experience-content ul li {
  margin-bottom: 10px;
  line-height: 1.6;
}

/* Footer */
footer {
  text-align: center;
  padding: 20px;
  background-color: #333;
  color: #fff;
  margin-top: 60px;
}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background: #007bff;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #0056b3;
}

::-webkit-scrollbar-track {
  background: #f8f9fa;
}

/* Media Queries */
@media (max-width: 768px) {
  .header-content {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .nav-links {
    flex-wrap: wrap;
    gap: 10px;
  }

  .section {
    padding: 20px 10px;
  }

  .project-card {
    flex-direction: column;
    align-items: center;
  }

  .experience-card {
    align-items: center;
    text-align: center;
  }
}
